<template>
  <div>
    <in-body :mbx="['首页','其他组件','myTree 目录树']">
      <a href="https://www.iviewui.com/components/tree" target="_blank">
        <Button type="primary">点击查看说明文档</Button>
      </a>

      <Divider orientation="left">基础用法</Divider>
      <Tree :data="data2" show-checkbox></Tree>
    </in-body>
  </div>

</template>

<script>
  import inBody from '../inBody'
  export default {
    components:{
      inBody
    }

    ,data () {
      return {
        data2: [
          {
            title: 'parent 1',
            expand: true,
            children: [
              {
                title: 'parent 1-1',
                expand: true,
                children: [
                  {
                    title: 'leaf 1-1-1'
                  },
                  {
                    title: 'leaf 1-1-2'
                  }
                ]
              },
              {
                title: 'parent 1-2',
                expand: true,
                children: [
                  {
                    title: 'leaf 1-2-1'
                  },
                  {
                    title: 'leaf 1-2-1'
                  }
                ]
              }
            ]
          }
        ]
      }
    }
  }
</script>

<style scoped>

</style>



